import React, { Component, Fragment } from 'react';
import { Card, Row, Col, Spin, message } from 'antd';

import { getStaffById } from 'services/teacher';
import { getStudentSubjectById, createTeacherSubject } from 'services/TeacherSubject';
import { getSubjectEduById } from 'services/subjectEdu';

import Page from 'components/LayoutComponents/Page';
import { getQueryParams } from 'utils/urlUtil';
import { BtnAddNew, BtnSave } from 'components/common/button';

import TableTeacherSubjectDetail from './TableTeacherSubjectDetail';
import ModalAddNewTeacherSubject from './ModalAddNewTeacherSubject';

class ManualSchedulePage extends Component {
  constructor(props) {
    super(props);
    this.state = {
      queryParams: {},
      openModal: false,
      loading: false,
      loadingModal: false,
      data: [],
      informationTeacher: {},
    };
  }

  componentDidMount = () => {
    const queryParams = getQueryParams();
    if (queryParams.teacherId) {
      this.setState({
        queryParams,
      });
      this.setState({
        loading: true,
      });
      this.getDataSubjects(queryParams.teacherId);
      this.getInformationTeacher(queryParams.teacherId);
      this.setState({
        loading: false,
      });
    }
  };

  onSaveTecherSubjects = async () => {
    const { loading } = this.state;
    if (loading) {
      return;
    }
    try {
      const { queryParams, data } = this.state;
      this.setState({
        loading: true,
      });
      const dataPost = {
        teacherId: "",
        subjectIds: [],
      };
      dataPost.teacherId = getQueryParams().teacherId;
      data.forEach((item) => {
        dataPost.subjectIds.push(item.subjectId);
      })
      await createTeacherSubject(dataPost);
      message.success('Lưu thành công.');
      this.setState({
        loading: false,
      }, () => {
        this.getDataSubjects(queryParams.teacherId);
      });
    } catch (error) {
      console.log(error);
    } finally {
      this.setState({
        loading: false,
      });
    }
  };

  onSaveModal = async (subject) => {
    this.setState({
      loadingModal: true,
    });
    const { data } = this.state;
    const id = subject.subjectId;
    const isDuplicate = data.some((item) => item.subjectId === id);
    if (isDuplicate) {
      message.warn("Môn học đã tồn tại.");
      this.setState({
        loadingModal: false,
      });
    } else {
      const response = await getSubjectEduById(id);
      response.data.subjectId = response.data.id;
      response.data.subject = response.data.label;
      data.push(response.data);
      this.setState({
        data,
        loadingModal: false,
      }, () => {
        this.closeModal();
      });
    }
  }

  onDeleteSubject = (subjectId) => {
    let { data } = this.state;
    data = data.filter(item => item.subjectId !== subjectId);
    this.setState({
      data,
    });
  }

  getDataSubjects = async (teacherId) => {
    try {
      const response = await getStudentSubjectById({ teacherId });
      this.setState({
        data: response.data.subjectList,
      });
    } catch (error) {
      console.log(error);
      this.setState({
        loading: false,
      });
    }
  }

  getInformationTeacher = async (teacherId) => {
    try {
      const id = teacherId;
      const response = await getStaffById(id);
      this.setState({
        informationTeacher: response.data,
      });
    } catch (error) {
      console.log(error);
      this.setState({
        loading: false,
      });
    }
  }

  openModal = () => {
    this.setState({
      openModal: true,
    });
  }

  closeModal = () => {
    this.setState({
      openModal: false,
    });
  }

  render() {
    const {
      queryParams,
      openModal,
      loading,
      data,
      informationTeacher,
      loadingModal
    } = this.state;
    return (
      <Fragment>
        <Spin spinning={loading}>
          <Card title="Giảng viên - Môn học chi tiết">
            <Row gutter={24}>
              <Col span={8}>
                <span style={{ fontWeight: "bold" }}>Giảng viên:</span> {informationTeacher.fullname} <br />
              </Col>
              <Col span={8}>
                <span style={{ fontWeight: "bold" }}>Giới tính:</span> {informationTeacher.genderText} <br />
              </Col>
            </Row>
            <Row gutter={24}>
              <Col span={8}>
                <span style={{ fontWeight: "bold" }}>Khoa:</span> {informationTeacher.faculty} <br />
              </Col>
              <Col span={8}>
                <span style={{ fontWeight: "bold" }}>Bộ môn:</span> {informationTeacher.subjectGroup} <br />
              </Col>
            </Row>
            <Row gutter={24}>
              <Col span={24}>
                <BtnAddNew onClick={this.openModal} />
                <BtnSave onClick={this.onSaveTecherSubjects} />
              </Col>
            </Row>
            <Row gutter={24}>
              <Col span={24}>
                <TableTeacherSubjectDetail
                  data={data}
                  onDelete={this.onDeleteSubject}
                />
              </Col>
            </Row>
          </Card>
        </Spin>
        {openModal &&
          <ModalAddNewTeacherSubject
            visible={openModal}
            closeModal={this.closeModal}
            onSaveModal={this.onSaveModal}
            teacherId={queryParams.teacherId}
            loading={loadingModal}
          />
        }
      </Fragment>
    );
  }
}

export default props => (
  <Page {...props}>
    <ManualSchedulePage />
  </Page>
);



